<template>
  <tr class="list-item">
    <td style="justify-content: flex-end">
        <input 
          class="cell-input form-control form-control-sm form-control-plaintext" 
          type="text" 
          v-model="node.n"
        >
    </td>
    <input-number class="cell" :node="node" :input-value="'x'"></input-number>
    <input-number class="cell" :node="node" :input-value="'y'"></input-number>
    <td class="cell-button">
        <button 
          class="btn btn-outline-secondary options" 
          type="button" 
          @click="showOptions=!showOptions"
        >
          <i :class="{ 'icon-expand-more': !showOptions, 'icon-expand-less': showOptions }"></i>
        </button>
        <button 
          class="btn btn-outline-danger delete" 
          type="button" 
          @click="$emit('remove')"
        >
          <i class="icon-close"></i>
        </button>
    </td>
    <input-number 
      class="cell row-options" 
      v-show="showOptions" 
      :node="node" 
      :input-value="'loadX'"
    >
      <input-number-checkbox :model="node" :val="'rx'" style="background-color: #eccdcd"></input-number-checkbox>
    </input-number>
    <input-number 
      class="cell row-options" 
      v-show="showOptions" 
      :node="node" 
      :input-value="'loadY'"
    >
      <input-number-checkbox :model="node" :val="'ry'" style="background-color: #c0e9c0"></input-number-checkbox>
    </input-number>
    <input-number 
      class="cell row-options" 
      v-show="showOptions" 
      :node="node" 
      :input-value="'loadZ'"
    >
      <input-number-checkbox :model="node" :val="'rz'" style="background-color: #c0d6ec"></input-number-checkbox>
    </input-number>
  </tr>
</template>

<script>
import InputNumber from './InputNumber'
import InputNumberCheckbox from './InputNumberCheckbox'

export default {
  name: 'NodeRow',
  components: {
    InputNumber, InputNumberCheckbox
  },
  props: ['node', 'nodes'],
	data() {
		return {
			showOptions: false,
		};
	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



